"use client";
import { useState, useEffect } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { AlertTriangle, Send, CheckCircle2, XCircle, Clock } from 'lucide-react';
import { toast } from 'sonner';
import { getRiskStudents, sendInterventionReminder } from '@/app/actions';

export default function RiskManagementPage() {
  const { currentUser } = useAppStore();
  const [students, setStudents] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  const loadData = async () => {
    const data = await getRiskStudents();
    setStudents(data);
    setLoading(false);
  };

  useEffect(() => {
    loadData();
  }, []);

  const handleRemind = async (studentId: string) => {
    if (!currentUser?.id) return;
    
    const res = await sendInterventionReminder(
      studentId, 
      currentUser.id, 
      "同学你好，系统检测到你最近压力可能较大，建议来心理中心聊聊，我们随时都在。"
    );

    if (res.success) {
      toast.success("已发送提醒通知");
      loadData(); // 刷新列表状态
    } else {
      toast.error("发送失败");
    }
  };

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-bold flex items-center gap-2 text-slate-800">
        <AlertTriangle className="text-red-500" /> 心理预警与干预
      </h1>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {students.length === 0 && !loading && (
           <div className="col-span-3 text-center py-10 text-slate-400">目前没有触发高风险预警的学生</div>
        )}
        
        {students.map((stu) => (
          <Card key={stu.id} className="border-l-4 border-l-red-500 shadow-sm hover:shadow-md transition-shadow">
            <CardContent className="p-5 space-y-4">
              {/* 头部信息 */}
              <div className="flex justify-between items-start">
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-red-50 text-red-600 rounded-full flex items-center justify-center font-bold text-lg">
                    {stu.name[0]}
                  </div>
                  <div>
                    <h3 className="font-bold text-lg">{stu.name}</h3>
                    <p className="text-xs text-slate-500">{stu.college} · {stu.username}</p>
                  </div>
                </div>
                <Badge variant="outline" className="text-red-600 border-red-200 bg-red-50">
                  得分: {stu.lastScore}
                </Badge>
              </div>

              {/* 状态描述 */}
              <div className="text-sm bg-slate-50 p-3 rounded text-slate-600">
                 最新测评时间: {stu.lastTestTime ? new Date(stu.lastTestTime).toLocaleDateString() : '未知'}
              </div>

              {/* 操作区：根据最近一次干预状态显示不同按钮 */}
              <div className="pt-2">
                {stu.latestIntervention?.status === 'pending' ? (
                   <Button disabled className="w-full bg-orange-100 text-orange-600 border-0">
                      <Clock className="w-4 h-4 mr-2"/> 等待学生回复
                   </Button>
                ) : stu.latestIntervention?.status === 'accepted' ? (
                   <Button disabled className="w-full bg-green-100 text-green-600 border-0">
                      <CheckCircle2 className="w-4 h-4 mr-2"/> 学生已接受预约
                   </Button>
                ) : stu.latestIntervention?.status === 'rejected' ? (
                   <div className="space-y-2">
                      <Button disabled className="w-full bg-red-100 text-red-600 border-0 mb-2">
                        <XCircle className="w-4 h-4 mr-2"/> 学生已拒绝
                      </Button>
                      <Button onClick={() => handleRemind(stu.id)} variant="outline" className="w-full text-slate-500">
                        再次提醒
                      </Button>
                   </div>
                ) : (
                   <Button onClick={() => handleRemind(stu.id)} className="w-full bg-red-600 hover:bg-red-700 text-white">
                      <Send className="w-4 h-4 mr-2"/> 发送预约提醒
                   </Button>
                )}
              </div>
            </CardContent>
          </Card>
        ))}
      </div>
    </div>
  );
}